import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActiveRouteGuard } from '../auth/activate-route-guard';

import { ContactusComponent } from './contactus/contactus.component';
import { StoryComponent } from './story/story.component';
import { VideosComponent } from './videos/videos.component';
import { CourseinstructionsComponent } from './courseinstructions/courseinstructions.component';
import { TalkAboutItComponent } from './talk-about-it/talk-about-it.component';
import { GroupCoachingComponent } from './group-coaching/group-coaching.component';
import { OnlineCourseComponent } from './online-course/online-course.component';
import { CorporationComponent } from './corporation/corporation.component';
import { YourCoursesComponent } from './your-courses/your-courses.component';
import { FreeExerciseComponent } from './free-exercise/free-exercise.component';
const routes: Routes = [
	{
		path: 'contactus',
		component: ContactusComponent,
    	canActivate: [ActiveRouteGuard]
	},
	{
		path: 'story',
		component: StoryComponent,
    	canActivate: [ActiveRouteGuard]
	},
	{
		path: 'videos',
		component: VideosComponent,
    	canActivate: [ActiveRouteGuard]
	},
	{
		path: 'courseinstructions',
		component: CourseinstructionsComponent
	},
	{
		path: 'talk-about-it',
		component: TalkAboutItComponent,
    	canActivate: [ActiveRouteGuard]
	},
	{
		path: 'group-coaching',
		component: GroupCoachingComponent
	},
	{
		path: 'online-course',
		component: OnlineCourseComponent
	},
	{
		path: 'corporation',
		component: CorporationComponent
	},
	{
		path: 'your-course',
		component: YourCoursesComponent
	},
	{
		path: 'free-exercise',
		component: FreeExerciseComponent
	}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PagesRoutingModule { }



